var inputName = $('input[name="name"]');
var inputEmail = $('input[name="email"]');

inputName.on('keypress', function (e) {
    var char = /["a-zA-Z]/;
    var val = String.fromCharCode(e.which);
    var test = char.test(val);

    if(!test) return false
});

inputEmail.on('keypress', function (e) {
    var char = /["а-яА-ЯёЁ]/;
    var val = String.fromCharCode(e.which);
    var test = char.test(val);

    if(test) return false
});

function valid(form) {
    $.validator.addMethod("minlenghtphone", function (value, element) {

            return value.replace(/\D+/g, '').length > 10;
        },
        "");
    $.validator.addMethod("requiredphone", function (value, element) {

            return value.replace(/\D+/g, '').length > 1;
        },
        "");
    $(form).validate({
        rules: {
            name: {
                required: true,
                minlength: 2
            },
            email: {
                required: true,
                email: true
            },
            password: {
                required: true,
                minlength: 6
            },
            password_old: {
                required: true,
                minlength: 6
            },
            password_confirmation: {
                minlength : 6,
                equalTo : ".password"
            },
            phone: {
                requiredphone: true,
                minlenghtphone: true
            },
            check: {
                required: true
            },
            address: {
                required: true,
                minlength: 5
            }
        },
        submitHandler: function submitHandler(form) {
            //alert("Submitted!");
            $.ajax({
                type: $(form).attr('method'),
                url: $(form).attr('action'),
                data: $(form).serialize(),
                success: function (data) {
                    if(parseInt(data) == 1) {
                        $(form).removeClass('form--active');
                        $(form).siblings('.form__ok').addClass('form--active');
                    } else {
                        $(form).removeClass('form--active');
                        $(form).siblings('.form__ok').addClass('form--active');
                    }
                },
                error: function () {
                    $(form).removeClass('form--active');
                    $(form).siblings('.form__ok').addClass('form--active');
                }
            });

            return false;
        }
    });
}

$('.modal__pas').on('click', function () {
    var input = $(this).prev('input');
   var type = input.attr('type') == "text" ? "password" : "text";
   $(this).toggleClass('modal__pas--active');
   input.prop('type', type);
});

